.btn {
    font-size: 11px;
    font-weight: 400;
    padding: 10px 15px;

    cursor: pointer;

    &-primary {
        position: relative;
        z-index: 1;

        color: $white;
        background-color: transparent;

        transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);

        &::before, &::after {
            content: '';
            position: absolute;	
            transition: inherit;
            left: 0;
            z-index: -1;
            height: 0;
            width: 100%;
        }

        &::before {
            bottom: 0;	
            border: 1px solid $white;
            border-top: 0;
            border-bottom: 0;
        }

        &::after {
            top: 0;
	        height: 0;
            border-right: 1px solid $white;
        }

        &:hover,
        &:focus {
            color: $dark-blue;
            transition-delay: .5s;

            &::before, &::after {
                height: 100%;
            }

            &::after {
                background-color: $white;
                transition-delay: .25s;
            }

            &::before {
                transition-delay: 0s;
            }
        }
    }
}